.app-wrapper {
  display: grid;
  grid-template-areas: 'h h'
    'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  gap: 10px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(247, 252, 252);
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}